export default "#chatContainer {\n  position: absolute;\n  z-index: 0;\n  width: 100%;\n  height: 100%;\n}\n\ninput {\n  font-family: 'Ubuntu', sans-serif;\n}\n\n#chatToolbar {\n  height: 30px;\n  background-color: #3d3d3d;\n  box-sizing: border-box;\n  font-weight: bold;\n  font-size: 14px;\n  text-align: center;\n  padding-top: 4px;\n  border-top-left-radius: 6px;\n  border-top-right-radius: 6px;\n  color: #ffffff;\n}\n\n#closeButton {\n  position: absolute;\n  right: 0;\n  top: -5px;\n}\n\n#chatComponent {\n  background-color: #b8b8b8;\n  position: absolute;\n  z-index: 99999;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  margin: auto;\n  height: calc(100% - 30px);\n  width: calc(100% - 30px);\n  border-radius: 20px;\n}\n\n.message-wrap {\n  padding: 0 15px;\n  height: calc(100% - 80px);\n  overflow: auto;\n}\n\n.message {\n  position: relative;\n  padding: 7px 0;\n}\n\n.user-img {\n  position: absolute;\n  border-radius: 45px;\n  width: 60px;\n  height: 60px;\n  top: 15px;\n}\n\n.msg-detail {\n  width: calc(100% - 65px);\n  display: inline-block;\n}\n\n.msg-detail p {\n  margin: 0;\n  font-size: 15px;\n}\n\n.msg-info p {\n  font-size: 0.8em;\n  color: #000000;\n  font-style: italic;\n}\n\n.msg-content {\n  position: relative;\n  margin-top: 5px;\n  border-radius: 5px;\n  padding: 8px;\n  color: #000000;\n  width: auto;\n  max-width: 95%;\n}\n\nspan.triangle {\n  border-radius: 2px;\n  height: 8px;\n  width: 8px;\n  top: 12px;\n  display: block;\n  transform: rotate(45deg);\n  position: absolute;\n}\n\n.text {\n  word-break: break-all;\n}\n\n/* Start message from other user */\n\n.message.left .msg-detail .msg-info {\n  text-align: left;\n}\n\n.message.left .msg-detail {\n  padding-left: 65px;\n}\n\n.message.left .user-img {\n  left: -5px;\n  border: 1px solid #f0f0f094;\n}\n\n.message.left .msg-detail .msg-content {\n  background-color: #f0f0f0;\n  float: left;\n}\n\n.message.left .msg-detail .msg-content span.triangle {\n  background-color: #f0f0f0;\n  border-bottom-width: 0;\n  border-left-width: 0;\n  left: -5px;\n}\n\n/* End message from other user */\n\n/* Start my messages */\n\n.message.right .msg-detail .msg-info {\n  text-align: right;\n}\n\n.message.right .user-img {\n  right: -5px;\n  border: 1px solid #c8ffe8ab;\n}\n\n.message.right .msg-detail .msg-content {\n  background-color: #c8ffe8;\n  float: right;\n}\n\n.message.right .msg-detail .msg-content span.triangle {\n  background-color: #c8ffe8;\n  border-bottom-width: 0;\n  border-left-width: 0;\n  right: -5px;\n}\n\n/* End my messages */\n\n#messageInput {\n  position: absolute;\n  bottom: 0px;\n  width: 100%;\n  background-color: #ffffff;\n  text-align: center;\n  padding: 10px 0px;\n  height: 30px;\n  border-bottom-left-radius: 6px;\n  border-bottom-right-radius: 6px;\n}\n\n#messageInput input {\n  width: 90%;\n  height: 100%;\n  border: none;\n  outline: none;\n  font-size: 14px;\n  margin-left: -6%;\n}\n\n#sendButton {\n  background-color: #81e9b0;\n  position: absolute;\n  right: 10px;\n  top: 0;\n  bottom: 0;\n  margin: auto;\n  border: 1px solid #7ae2a9;\n  box-shadow: none !important;\n}\n\n#sendButton mat-icon {\n  margin-left: 3px !important;\n  margin-bottom: 2px !important;\n}\n\n::-webkit-scrollbar {\n  width: 8px;\n}\n\n::-webkit-scrollbar-thumb {\n  background-color: #6b6b6b;\n}\n\n.chatComponentLight ::-webkit-scrollbar-thumb {\n  background-color: #eeeeee !important;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"